@import url('https://fonts.googleapis.com/css2?family=Abril+Fatface&family=Fredoka+One&family=Permanent+Marker&display=swap');
.bg-img{
    background-image: url('../images/header3.jpg');
    background-position: center;
    /* background-position: ;00 */
    background-size: cover;
    height: 100%;
    /* -webkit-transform: rotate(180deg);
    -moz-transform: rotate(180deg); */
    background-repeat: no-repeat;
}
.header{
    height: 87vh;
}
.h-text{
    z-index: 1;
}
.h-text p:nth-child(1){
    color: rgb(39, 92, 19);
    font-family: 'Permanent Marker', cursive;
    font-size: 40px;
}
.h-text p:nth-child(2){
    color: #274c5b;
    /* font-family: 'Permanent Marker', cursive; */
    font-size: 60px;
    font-family: 'Abril Fatface', cursive;
}
.h-text a{
    background-color: #0d3101;
    color: white;
}
.h-text a:hover{
    background-color: #efd372;
}
